  border-radius: .9375rem;
  max-width: 45rem;
  height: 5.4375rem;
  padding-left: 1rem;
  font-family: Plus Jakarta Sans;
  font-size: 1.728rem;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  box-shadow: 0 4px 4px #00000040;
}

.btn-add-player {
  color: #fff;
  background: #0f4016;
