  font-size: 1.44rem;
  font-style: normal;
  font-weight: 400;
}

.name-list {
  background: #d9d9d96b;
  border: 5px solid #000;
  min-height: 50rem;
  max-height: 50rem;
  margin: 10%;
  padding-left: 0;
  list-style: none;
  overflow-y: scroll;
}

.name-list-item {
